html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  padding: 0;
  margin: 0;
  font-size: 1.125rem;
  font-weight: 700;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --very-dk-gray: hsl(0, 0%, 17%);
  --dk-gray: hsl(0, 0%, 59%);
}

:root {
  --font-body: "Rubik", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}